/**
 * API client for FastAPI backend
 */
import axios from 'axios';

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000';

const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Analytics API
export const analyticsApi = {
  getSummary: () => api.get('/api/analytics/summary').then(res => res.data),
  getVendorStats: (limit = 10) => api.get(`/api/analytics/vendor-stats?limit=${limit}`).then(res => res.data),
  getCategoryBreakdown: () => api.get('/api/analytics/category-breakdown').then(res => res.data),
  getTimeSeries: () => api.get('/api/analytics/time-series').then(res => res.data),
  getForecast: (months = 3) => api.get(`/api/analytics/forecast?months=${months}`).then(res => res.data),
};

// Documents API
export const documentsApi = {
  getAll: (skip = 0, limit = 100, docType?: string) => {
    const params = new URLSearchParams({ skip: skip.toString(), limit: limit.toString() });
    if (docType) params.append('doc_type', docType);
    return api.get(`/api/documents?${params}`).then(res => res.data);
  },
  getById: (id: number) => api.get(`/api/documents/${id}`).then(res => res.data),
  upload: async (file: File) => {
    const formData = new FormData();
    formData.append('file', file);
    return api.post('/api/documents/upload', formData, {
      headers: { 'Content-Type': 'multipart/form-data' },
    }).then(res => res.data);
  },
  update: (id: number, updates: any) => api.put(`/api/documents/${id}`, updates).then(res => res.data),
  getSimilar: (id: number, limit = 5) => api.get(`/api/documents/${id}/similar?limit=${limit}`).then(res => res.data),
};

// Chat API
export const chatApi = {
  query: (query: string, useRag = true, useSql = true) =>
    api.post('/chat/insights', { query, use_rag: useRag, use_sql: useSql }).then(res => res.data),
};

// Anomalies API
export const anomaliesApi = {
  getAll: () => api.get('/api/anomalies').then(res => res.data),
};

// Document Comparison API
export const comparisonApi = {
  compare: (doc1Id: number, doc2Id: number) =>
    api.post('/api/documents/compare', { doc1_id: doc1Id, doc2_id: doc2Id }).then(res => res.data),
  getPriceChanges: (vendor: string) => api.get(`/api/vendors/${encodeURIComponent(vendor)}/price-changes`).then(res => res.data),
};

// Insights API
export const insightsApi = {
  getReport: () => api.get('/api/insights/report').then(res => res.data),
};

// Receipt Matching API
export const receiptApi = {
  getUnmatched: () => api.get('/api/receipts/unmatched').then(res => res.data),
  match: (receiptId: number) => api.post(`/api/receipts/${receiptId}/match`).then(res => res.data),
};

// Export API
export const exportApi = {
  excel: () => api.get('/api/export/excel', { responseType: 'blob' }).then(res => {
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'expense_report.xlsx');
    document.body.appendChild(link);
    link.click();
    link.remove();
  }),
  summary: () => api.get('/api/export/summary').then(res => res.data),
};

export default api;

