'use client';

import { useEffect, useState } from 'react';
import Layout from '@/components/Layout';
import { analyticsApi } from '@/lib/api';
import { ChartBarIcon, DocumentTextIcon, ExclamationTriangleIcon } from '@heroicons/react/24/outline';
import { BanknotesIcon } from '@heroicons/react/24/solid';
import Link from 'next/link';
import toast from 'react-hot-toast';

interface Summary {
  total_transactions: number;
  total_spend: number;
  avg_transaction: number;
  total_documents: number;
}

export default function Home() {
  const [summary, setSummary] = useState<Summary | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchSummary = async () => {
      try {
        const data = await analyticsApi.getSummary();
        setSummary(data);
      } catch (error) {
        toast.error('Failed to load summary');
        console.error(error);
      } finally {
        setLoading(false);
      }
    };
    fetchSummary();
  }, []);

  const stats = [
    {
      name: 'Total Transactions',
      value: summary?.total_transactions.toLocaleString() || '0',
      icon: ChartBarIcon,
      href: '/analytics',
      color: 'bg-blue-500',
    },
    {
      name: 'Total Spending',
      value: `$${summary?.total_spend.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}`,
      icon: BanknotesIcon,
      href: '/analytics',
      color: 'bg-green-500',
    },
    {
      name: 'Documents',
      value: summary?.total_documents.toLocaleString() || '0',
      icon: DocumentTextIcon,
      href: '/documents',
      color: 'bg-purple-500',
    },
    {
      name: 'Avg Transaction',
      value: `$${summary?.avg_transaction.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}`,
      icon: BanknotesIcon,
      href: '/analytics',
      color: 'bg-yellow-500',
    },
  ];

  return (
    <Layout>
      <div className="space-y-6">
        {/* Welcome Section */}
        <div className="bg-gradient-to-br from-blue-600 via-blue-700 to-indigo-800 rounded-xl shadow-xl p-8 text-white overflow-hidden relative">
          <div className="absolute inset-0 bg-grid-pattern opacity-10"></div>
          <div className="relative z-10">
            <h1 className="text-3xl font-bold mb-3">Welcome to Insight Agent IDP</h1>
            <p className="text-blue-100 text-lg max-w-2xl">
              AI-Powered Intelligent Document Processing with advanced analytics, insights, and automated data extraction
            </p>
          </div>
        </div>

        {/* Quick Stats */}
        {loading ? (
          <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
            {[1, 2, 3, 4].map((i) => (
              <div key={i} className="bg-white overflow-hidden shadow rounded-lg animate-pulse">
                <div className="p-5 h-24 bg-gray-200"></div>
              </div>
            ))}
          </div>
        ) : (
          <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
            {stats.map((stat) => (
              <Link
                key={stat.name}
                href={stat.href}
                className="bg-white overflow-hidden shadow-md rounded-xl hover:shadow-xl transition-all duration-200 hover:-translate-y-1 border border-gray-100"
              >
                <div className="p-6">
                  <div className="flex items-center">
                    <div className={`${stat.color} p-3 rounded-lg shadow-sm`}>
                      <stat.icon className="h-6 w-6 text-white" />
                    </div>
                    <div className="ml-4 flex-1 min-w-0">
                      <p className="text-sm font-medium text-gray-600 truncate">{stat.name}</p>
                      <p className="text-2xl font-bold text-gray-900 mt-1">{stat.value}</p>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* Quick Actions */}
        <div className="bg-white shadow-md rounded-xl p-6 border border-gray-100">
          <h2 className="text-xl font-semibold text-gray-900 mb-6">Quick Actions</h2>
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-3">
            <Link
              href="/documents"
              className="flex items-start p-5 border-2 border-gray-200 rounded-xl hover:border-blue-500 hover:bg-blue-50 transition-all duration-200 hover:shadow-md group"
            >
              <div className="flex-shrink-0">
                <div className="bg-blue-100 p-3 rounded-lg group-hover:bg-blue-200 transition-colors">
                  <DocumentTextIcon className="h-6 w-6 text-blue-600" />
                </div>
              </div>
              <div className="ml-4">
                <div className="font-semibold text-gray-900 group-hover:text-blue-700">Upload Document</div>
                <div className="text-sm text-gray-600 mt-1">Process new invoices or receipts</div>
              </div>
            </Link>
            <Link
              href="/analytics"
              className="flex items-start p-5 border-2 border-gray-200 rounded-xl hover:border-green-500 hover:bg-green-50 transition-all duration-200 hover:shadow-md group"
            >
              <div className="flex-shrink-0">
                <div className="bg-green-100 p-3 rounded-lg group-hover:bg-green-200 transition-colors">
                  <ChartBarIcon className="h-6 w-6 text-green-600" />
                </div>
              </div>
              <div className="ml-4">
                <div className="font-semibold text-gray-900 group-hover:text-green-700">View Analytics</div>
                <div className="text-sm text-gray-600 mt-1">Explore spending trends and insights</div>
              </div>
            </Link>
            <Link
              href="/anomalies"
              className="flex items-start p-5 border-2 border-gray-200 rounded-xl hover:border-red-500 hover:bg-red-50 transition-all duration-200 hover:shadow-md group"
            >
              <div className="flex-shrink-0">
                <div className="bg-red-100 p-3 rounded-lg group-hover:bg-red-200 transition-colors">
                  <ExclamationTriangleIcon className="h-6 w-6 text-red-600" />
                </div>
              </div>
              <div className="ml-4">
                <div className="font-semibold text-gray-900 group-hover:text-red-700">Check Anomalies</div>
                <div className="text-sm text-gray-600 mt-1">Detect duplicates and issues</div>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </Layout>
  );
}
